import { Badge, Button, Form, Modal } from "react-bootstrap";
import { useState } from "react";
import { TOAST_ERROR, TOAST_SUCCESS, validateEmail } from "../../../../utils";
import { shareCode } from "../../../../actions/publishAction";
import { TAGGHOP_SHARE_CONNECTION_URL } from "../../../../actions/sceneActions";
import { FontAwesomeIcon } from "../../../../Assets/AssetsHelper";


const ShareCodeModal = ({ type, from, text, heading, btnSendText, onClose, shareUrl, codeId, isShare }) => {

  const [loader, setLoader] = useState(false);
  const [emailError, setEmailError] = useState(false)
  const [emailId, setEmailId] = useState('')
  const [emails, setEmails] = useState([]);
  const htmlCode = document.querySelector(codeId)?.innerText || shareUrl

  const sendCode = () => {
    if (emails.length > 0) {
      setLoader(true);
      if (!isShare) {
        const code = { 'email': emails, 'type': type ? type : from == 'reviewHub' ? 6 : 3, 'htmlCode': from == 'reviewHub' ?  document.querySelector(codeId)?.value || shareUrl : htmlCode }

        shareCode(code).then((res) => {
          if (res.data.responseCode == 200) {
            setTimeout(() => {
              if (from == 'reviewHub') TOAST_SUCCESS('Successfully shared.')
              else TOAST_SUCCESS('Code successfully shared.')
              setLoader(false)
              onClose()
            }, 1000);

          } else {
            setLoader(false)
            TOAST_ERROR(res.data.message);
          }
        }).catch((error) => {
          setLoader(false);
          TOAST_ERROR('Something went wrong, please try again later.');
        })
      }

      else {
        TAGGHOP_SHARE_CONNECTION_URL({ emailIds: emails, shareURL: shareUrl }).then((response) => {
          const { responseData, responseCode } = response.data;

          if (responseCode == 200) {
            TOAST_SUCCESS('URL shared successfully.');
            setLoader(false);
            onClose()
          }
        }).catch((error) => {
          const { data } = error.response;
          setLoader(false);
          TOAST_ERROR('Something went wrong, please try again later.');
        });
      }
    }
    else {
      setLoader(false)
      TOAST_ERROR('Please fill Email Id');
    }
  }

  const onChangeEmail = event => setEmailId(event.target.value)

  const onSelectEmail = () => {
    if (emailId.length > 0 && !emails.includes(emailId) && validateEmail(emailId)) {
      emails.push(emailId)
      setEmails(emails)
      setEmailId('')
      setEmailError(false)
    }
    else setEmailError(true)
  }

  const onRemoveEmail = (index) => {
    const newEmails = emails.filter((email, i) => i !== index);
    setEmails(newEmails)
  }

  return (
    <>
      <Modal show={true} onHide={onClose} aria-labelledby="contained-modal-title-vcenter" centered >
        <Modal.Header closeButton>
          <Modal.Title>{heading}</Modal.Title>
        </Modal.Header>
        <Modal.Body>

          <div className="mx-xl-4 px-2 px-lg-4 mx-0 pt-4" style={{ minHeight: 320 }}>
            <p className="text-center mb-4">{text}</p>
            <div className="mb-3 mb-xl-4 d-flex align-items-start">
              <div className='d-flex flex-column w-100 pe-2'>
                <Form.Control className={`position-relative d-flex flex-grow-1 flex-column ${emailError ? 'is-invalid' : ''}`} placeholder="example@example.com" value={emailId} onKeyDown={event => event.key == "Enter" ? onSelectEmail() : null} onChange={onChangeEmail}></Form.Control>
                {emailError && <div className="invalid-feedback d-block">Enter valid email.</div>}
              </div>
              <Button variant="primary" className="d-flex align-items-center" onClick={onSelectEmail}>
                <FontAwesomeIcon icon={'plus'} />
                Add
              </Button>
            </div>

            <div className="mb-3 mb-xl-4">
              <div className="tags-group d-flex align-items-center flex-wrap">
                {emails.length > 0 ? emails.map((email, index) => {
                  return <Badge key={email.toString()} bg="" className="badge-secondary px-3 py-2">
                    {email}
                    <Button variant="close" size="sm" className="p-0 ms-1" onClick={() => onRemoveEmail(index)}></Button>
                  </Badge>
                }) : null}
              </div>
            </div>
          </div>
        </Modal.Body>
        <Modal.Footer className="p-3">
          <div className="mx-xl-4 px-2 px-lg-4 mx-0">
            <Button type="button" className="btn btn-secondary btn-min-w me-3" onClick={() => onClose()}>Cancel</Button>
            <Button disabled={emails.length > 0 ? false : true} type="button" className={`btn btn-primary btn-min-w ${loader ? `spinner` : ``}`} onClick={() => sendCode(emails.toString())}>{btnSendText ? btnSendText : `Share Code`}</Button>
          </div>
        </Modal.Footer>
      </Modal>
    </>
  )
}

export default ShareCodeModal