import React, { Component } from 'react';
import { Modal } from 'react-bootstrap';
import ProfileImage from '../CommonComponents/ProfileImage/ProfileImage';
import { GET_NETWORK_ICON } from '../../../../utils';
import { baseUrl } from '../../../../Assets/AssetsHelper';

class PagesModal extends Component {

    handleClose = () => {
        this.props.modalCallback(false)
    };

    render() {
        const { modalToggle, childConnectionList } = this.props

        return <Modal show={modalToggle} onHide={this.handleClose}
            aria-labelledby="contained-modal-title-vcenter"
            centered
        >
            <Modal.Header closeButton>
                <Modal.Title>Social Pages</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <div className="connection_modal mx-xl-4 px-2 px-lg-4 mx-0 overflow-auto" style={{height:'calc(100vh - 150px)'}}>
                    {
                        childConnectionList.map((item) => {
                            return <div className="d-flex justify-content-between align-items-center py-3 border-bottom">
                                <div className="d-flex align-items-center flex-grow-1 me-2 w-75">
                                    <ProfileImage className={'symbol symbol-40 rounded-circle flex-shrink-0 me-2 overflow-hidden'}
                                        imageSize={40}
                                        symbolClass={'symbol-label'}
                                        name={item.ConnectedAccounts.value2} profileUrl={item.ConnectedAccounts.profilePicture} networkId={item.ConnectedAccounts.networkId} />
                                    <div className="overflow-hidden">
                                        <p className="mb-0 w-100 fw-bold text-ellipsis">{item.ConnectedAccounts.value2}</p>
                                        <p className="mb-0 text-gray-600 text-ellipsis mb-0 fs-8">
                                            @{item.ConnectedAccounts.value1}
                                        </p>
                                    </div>
                                </div>
                                <div className="d-flex flex-column align-items-end flex-srink-0">
                                    <div className="fs-8 fw-bold"> <img className="img-fluid h-100" src={baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(item.ConnectedAccounts.networkId)}.svg`)} height={28} width={28} alt="" /></div>
                                    <div className="fs-8 text-muted fw-medium d-none">Posts</div>
                                </div>
                            </div>
                        })
                    }
                </div>
            </Modal.Body>
        </Modal>

    }
}

export default PagesModal;