import React, { Component } from 'react';
import { Image } from 'react-bootstrap';
import { baseUrl } from '../../../../../Assets/AssetsHelper';

class SearchNotFound extends Component {

       render() {
              const { title, desc, icon, height } = this.props
              return <div className={`f-center py-4 h-100 w-100`} style={{minHeight:height}}>
                     <div className="d-flex flex-column align-items-center">
                            <div className="mb-2 text-center">
                                   <Image data-mode="light" className="mb-4" src={baseUrl(`media/icons/color-icons/${icon}.svg`)} height={70} width={70} alt={icon} fluid/>
                                   <Image data-mode="dark" className="mb-4" src={baseUrl(`media/icons/color-icons/${icon}-light.svg`)} height={70} width={70} alt={icon} fluid/>
                                   <h4 className={`text-gray-dark fw-semibold text-center`}>{title}</h4>
                                   {desc ? <p className={"fs-7"}>{desc}</p> : null}
                            </div>
                     </div>
              </div>
       }
}

export default SearchNotFound;