import { Component, Suspense } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { DELETE_USER_SOCIAL_ACCOUNT, TAGGSHOP_SOCIAL_ACCOUNT_DATA, TAGSHOP_SOCIAL_ACCOUNT_ADD_ACCOUNT_DATA } from "../../../../actions/sceneActions";
import { Loader } from "../CommonComponents/Loader";
import SearchNotFound from "../CommonComponents/NotFound/SearchNotFound";
import ProfileImage from "../CommonComponents/ProfileImage/ProfileImage";
import { COPY_TO_CLIPBOARD, GET_NETWORK_ICON, GET_NETWORK_NAME, showAddAccounntAlert, TOAST_SUCCESS } from "../../../../utils";
import { baseUrl, FontAwesomeIcon } from "../../../../Assets/AssetsHelper";
import { Button, OverlayTrigger, Tooltip, Container, Card, Table, Alert, Form } from "react-bootstrap";
import moment from "moment";
import PagesModal from "./PagesModal";
import ShareCodeModal from "../CommonComponents/ShareCodeModal";
import AddSocialAccount from "./AddSocialAccount";
import Swal from "sweetalert2";
import { addAccountFromId } from "../../../../utils/addAccount";
import { showUpgrade } from "../../../../actions/collaboratorActions";
import withRouter from "../../../routes/WithRouter";
import { HOST_URL } from "../../../../actions/api";

const channel = new BroadcastChannel('connect');
class SocialAccount extends Component {

    constructor() {
        super();
        this.controller = new AbortController();
    }

    state = {
        showModal: false,
        addAccountModal: false,
        childConnectionList: [],
        codeCopied: false,
        shareUrlModal: false,
        isLoading: false,
    };

    componentDidMount() {
        const { socialAccountsData, networkList } = this.props
        if (!(socialAccountsData && Object.keys(socialAccountsData).length > 0)) this.props.TAGGSHOP_SOCIAL_ACCOUNT_DATA();
        if (!networkList) this.props.TAGSHOP_SOCIAL_ACCOUNT_ADD_ACCOUNT_DATA()
        window.addEventListener("message", this.handleMessage, { signal: this.controller.signal });
        channel.addEventListener("message", this.handleMessage);
    }

    componentWillUnmount() {
        window.removeEventListener("message", this.handleMessage, { signal: this.controller.signal });
        channel.removeEventListener("message", this.handleMessage);
        this.controller.abort();
        channel.close();
    }

    handleMessage = async (event) => {
        try {
            const { TAGGSHOP_SOCIAL_ACCOUNT_DATA } = this.props;
            const response = JSON.parse(event.data);
            const { code } = response;
            const networkId = localStorage.getItem('networkId');
            const type = localStorage.getItem('connectionType');
            if (event.origin.includes("tagshop.ai") || event.origin.includes("localhost")) {
                if (type == 'socialConnect') {
                    if (code) await addAccountFromId(parseInt(networkId), code, TAGGSHOP_SOCIAL_ACCOUNT_DATA);
                }
            }
        } catch (error) {
            console.log("--- handleMessage error ---", error);
        }
    };

    handleModalShow = (data) => event => {
        this.setState({
            showModal: true,
            childConnectionList: data
        })
    }

    handleCallback = (dataEvent) => {
        this.setState({
            showModal: dataEvent
        })
    }

    shareUrlCallback = () => {
        this.setState({ shareUrlModal: false })
    }

    codeCopy = (id) => {
        this.setState({
            codeCopied: true
        })
        setTimeout(() => {
            this.setState({
                codeCopied: false
            })
        }, 1500);
        COPY_TO_CLIPBOARD(id);
    }

    shareUrlOpenModal = event => this.setState({
        shareUrlModal: true
    })

    addAccountCallback = (dataEvent) => {
        this.setState({ addAccountModal: dataEvent })
    }

    reconnectAccount = (item) => {
        const typeValue = window.location.pathname.includes('connection') ? 'connectAccount' : 'socialConnect';
        localStorage.setItem("connectionType", typeValue)
        this.setState({ addAccountModal: false }, () => showAddAccounntAlert(item, true));
    }

    onClickDeleteAccount = (accountId, networkId) => {
        Swal.fire({
            title: 'Are you sure?',
            text: "You want to delete this",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonText: 'Yes, delete it!'
        }).then((result) => {
            if (result.isConfirmed) {
                var self = this;
                self.props.DELETE_USER_SOCIAL_ACCOUNT(accountId, networkId, () => this.setState({ isLoading: !this.state.isLoading }, () => TOAST_SUCCESS('Account successfully disconnected.')));
            }
        })
    }

    render() {
        const { socialAccountsData, networkList, isSharing, userData, navigate } = this.props
        const { codeCopied, childConnectionList, showModal, shareUrlModal, addAccountModal } = this.state
        return <div className={`d-flex flex-column flex-column-fluid full-content ${isSharing ? 'mt-lg-10 mt-5' : ''}`}>
            {!isSharing ? <div className="subheader">
                <Container fluid>
                    <div className="nav nav-underline">
                        <div className='nav-item'>
                            <Link to={'/profile'} className="nav-link">Profile</Link>
                        </div>
                        {userData && (userData.collaborator == '' || userData.collaborator?.activeAccount == 0) ? <div className='nav-item'>
                            <Link to={'/transactions'} className="nav-link">Plan Details</Link>
                        </div> : null}
                        <div className='nav-item'>
                            <Link to={'/social-account'} className="nav-link active">Social Accounts</Link>
                        </div>
                        {userData && (userData.collaborator == '' || userData.collaborator?.activeAccount == 0 || userData.collaborator?.permissions == '') ? <div className='nav-item'>
                            <button onClick={() => userData.collaboratorAccess ? navigate('/collaborator') : showUpgrade(navigate)} className="nav-link">Collaborator</button>
                        </div> : null}
                    </div>
                </Container>
            </div> : true}
            <Container>
                <Card className="mb-xl-5 mb-4">
                    <Card.Header className="f-between flex-wrap w-100">
                        <h2 className="fs-6 text-gray-800 my-3">Social Accounts</h2>
                        <div className="my-2">
                            <Button className="btn-min-w" onClick={() => this.setState({ addAccountModal: true })}>
                                <FontAwesomeIcon type="solid" icon={'plus'} iconClass={'me-1'} />Add Account
                            </Button>
                        </div>
                    </Card.Header>
                    <Card.Body className="d-flex flex-column justify-content-between" style={{ minHeight: 'calc(100vh - 220px)' }}>
                        {!socialAccountsData ?
                            <Loader />
                            :
                            <>
                                {socialAccountsData.accountInformations && socialAccountsData.accountInformations.length > 0 ?
                                    <div className="d-flex flex-column w-100 flex-grow-1 mb-3">
                                        <Table size="xl" className="align-middle gs-0 gy-3 my-0" responsive>
                                            <thead className="thead-light">
                                                <tr>
                                                    <th className="fw-bold text-nowrap" style={{ minWidth: 170 }}>Account</th>
                                                    <th className="fw-bold text-nowrap" style={{ minWidth: 70 }}>Network</th>
                                                    <th className="fw-bold text-nowrap" style={{ minWidth: 150 }}>Account Validity</th>
                                                    <th className="fw-bold text-nowrap" style={{ minWidth: 150 }}>Pages</th>
                                                    <th className="fw-bold text-nowrap" style={{ minWidth: 70 }}>Status</th>
                                                    <th className="fw-bold text-nowrap text-end" style={{ minWidth: 70 }}>Actions</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {
                                                    socialAccountsData.accountInformations && socialAccountsData.accountInformations.map((accItem, accIndex) => {
                                                        return <tr key={accIndex}>
                                                            <td>
                                                                <div className="d-flex align-items-center">
                                                                    <ProfileImage className={'symbol symbol-40 symbol-circle me-2'}
                                                                        imageSize={40}
                                                                        symbolClass={'symbol-label text-gray-100-alt fs-5'}
                                                                        name={accItem.ConnectedAccounts.accountName} profileUrl={accItem.ConnectedAccounts.profilePicture} networkId={accItem.Network.id} />
                                                                    <div>
                                                                        <p className="text-gray-800 mb-0 fw-bold">{accItem.ConnectedAccounts.accountName}</p>
                                                                        <small className="mb-0 text-gray-600">@{accItem.ConnectedAccounts.accountName}</small>
                                                                    </div>
                                                                </div>
                                                            </td>
                                                            <td>
                                                                <div className="w-100">
                                                                    <OverlayTrigger placement="top" overlay={<Tooltip>{GET_NETWORK_NAME(accItem.Network.id)}</Tooltip>}>
                                                                        <span className="h-24px w-24px d-flex">
                                                                            <img className="img-fluid h-100" src={baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(accItem.Network.id)}.svg`)} height={24} width={24} alt={GET_NETWORK_NAME(accItem.Network.id)} data-invert={GET_NETWORK_NAME(accItem.Network.id)} />
                                                                        </span>
                                                                    </OverlayTrigger>
                                                                </div>
                                                            </td>
                                                            <td>
                                                                <p className="fs-8 text-gray-800 fw-semibold mb-0">{moment(new Date(accItem.ConnectedAccounts.validity * 1000)).format("DD MMM YYYY")}</p>
                                                                <span className="fs-9 text-gray-600">{moment(new Date(accItem.ConnectedAccounts.validity * 1000)).format("hh:mm A")}</span>
                                                            </td>

                                                            <td>
                                                                {accItem.ConnectedAccounts.child.length > 0 ?
                                                                    <div className="symbol-group symbol-hover flex-nowrap">
                                                                        {accItem.ConnectedAccounts.child.slice(0, 6).map((childItem, childIndex) => {
                                                                            return <OverlayTrigger key={childIndex} placement="top" overlay={<Tooltip className="text-start">
                                                                                <p className="text-gray-100 mb-0 w-100 fw-bold text-ellipsis text-start">{childItem.ConnectedAccounts.name && String(childItem.ConnectedAccounts.name).length > 0 ? childItem.ConnectedAccounts.name : childItem.ConnectedAccounts.value2}</p>
                                                                                <p className="mb-0 text-gray-300 text-ellipsis mb-0 fs-8 text-start">
                                                                                    {childItem.ConnectedAccounts.networkId == 18 ? `@` : ``}{childItem.ConnectedAccounts.value2}
                                                                                </p>
                                                                            </Tooltip>}>
                                                                                <span>
                                                                                    <ProfileImage className={'symbol symbol-36 symbol-circle bg-light'}
                                                                                        imageSize={36}
                                                                                        symbolClass={'symbol-label text-gray-100-alt fs-8'}
                                                                                        name={childItem.ConnectedAccounts.value2} profileUrl={childItem.ConnectedAccounts.profilePicture} networkId={accItem.Network.id} />
                                                                                </span>
                                                                            </OverlayTrigger>
                                                                        })
                                                                        }
                                                                        {accItem.ConnectedAccounts.child.length > 6 ?
                                                                            <div className="symbol symbol-36 symbol-circle cursor-pointer" onClick={this.handleModalShow(accItem.ConnectedAccounts.child)}>
                                                                                <span className="symbol-label bg-dark fs-8 fw-semibold">+{accItem.ConnectedAccounts.child.length}</span>
                                                                            </div>
                                                                            : null}
                                                                    </div>
                                                                    : '--'}
                                                            </td>
                                                            <td>
                                                                <span className={`badge badge-light-${(accItem.ConnectedAccounts.api == 3 || accItem.ConnectedAccounts.api == 4) ? `danger` : `success`} py-1 px-2`}>{accItem.ConnectedAccounts.api == 3 || accItem.ConnectedAccounts.api == 4 ? `Reconnect` : `Connected`}</span>
                                                            </td>
                                                            <td className="pr-0 text-end">
                                                                <div className="d-flex align-items-center justify-content-end">
                                                                    <OverlayTrigger placement="top" overlay={<Tooltip>{accItem.ConnectedAccounts.api == 4 ? `Reconnect` : `Refresh`}</Tooltip>}>
                                                                        <Button variant="icon" size="sm" className="btn-active-primary me-3" onClick={() => this.reconnectAccount(accItem.Network, accItem.ConnectedAccounts.api)}>
                                                                            <FontAwesomeIcon icon={'arrows-rotate'} />
                                                                        </Button>
                                                                    </OverlayTrigger>
                                                                    {!isSharing ? <OverlayTrigger placement="top" overlay={<Tooltip>Remove</Tooltip>}>
                                                                        <Button variant="icon" size="sm" className="btn-active-primary" onClick={() => this.onClickDeleteAccount(accItem.ConnectedAccounts.id, accItem.ConnectedAccounts.networkId)}>
                                                                            <FontAwesomeIcon type="solid" icon={'trash-can'} />
                                                                        </Button>
                                                                    </OverlayTrigger> : null}
                                                                </div>
                                                            </td>
                                                        </tr>
                                                    })
                                                }
                                            </tbody>
                                        </Table>
                                    </div>
                                    :
                                    <Suspense fallback={<></>}>
                                        <SearchNotFound title='Nothing to see here!' desc='Uh-oh! No social networks connected. Link your connections and collect more content!' height={'calc(100vh - 415px)'} icon='product'></SearchNotFound>
                                    </Suspense>}

                                {!isSharing ? <div className={`col-lg-6`}>
                                    <div className="mb-3 mb-xl-4">
                                        <p className="mb-2 text-gray-800 fw-semibold">Want to add social account without asking for credentials? Just share the link with the owner.</p>
                                        <div className="d-flex align-items-center w-100">
                                            <div className="position-relative d-flex flex-grow-1 flex-column pe-2">
                                                <OverlayTrigger placement="top" overlay={<Tooltip>{!codeCopied ? 'Copy Url' : 'Url Copied'}</Tooltip>}>
                                                    <Form.Control type="text" name="ShareURL" id="ShareURL" size="lg" placeholder="ShareURL" onClick={() => this.codeCopy('ShareURL')} value={`${HOST_URL}connection/${localStorage.getItem('ownerId')}`} />
                                                </OverlayTrigger>
                                            </div>
                                            <button type="button" className="btn btn-lg btn-primary" onClick={this.shareUrlOpenModal}>Share</button>

                                        </div>
                                    </div>
                                </div> : null}

                                <Alert variant="warning" className="p-3 rounded-1">
                                    Tagshop cannot access, store, or view any personal information or passwords that you may use to connect your social media accounts.
                                </Alert>
                            </>}
                    </Card.Body>
                </Card>
                {childConnectionList && childConnectionList.length > 0 ? <Suspense fallback={<></>}>
                    <PagesModal modalCallback={this.handleCallback} modalToggle={showModal} childConnectionList={childConnectionList} />
                </Suspense> : null}
                {networkList && networkList.length > 0 ? <Suspense fallback={<></>}>
                    <AddSocialAccount addAccountCallback={this.addAccountCallback} reconnectAccount={this.reconnectAccount} modalToggle={addAccountModal} networkList={networkList} />
                </Suspense> : null}
                <Suspense fallback={<></>}>
                    {shareUrlModal ? <ShareCodeModal text="Send the connection URL to the account's owner. Enter the email id to share the link directly with them" heading="Share Connection URL By Email" onClose={this.shareUrlCallback} codeId={'#buttonEmbedCode'} shareUrl={`${HOST_URL}connection/${localStorage.getItem('ownerId')}`} isShare={true} /> : null}
                </Suspense>
            </Container>
        </div>
    }
}

const mapStateToProps = state => {
    return {
        socialAccountsData: state.userAccountData?.socialAccounts,
        networkList: state.userAccountData?.addAccount,
        social: state.socialAccounts,
        userData: state.authorized?.userData?.user_data,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        TAGGSHOP_SOCIAL_ACCOUNT_DATA: () => dispatch(TAGGSHOP_SOCIAL_ACCOUNT_DATA()),
        TAGSHOP_SOCIAL_ACCOUNT_ADD_ACCOUNT_DATA: () => dispatch(TAGSHOP_SOCIAL_ACCOUNT_ADD_ACCOUNT_DATA()),
        DELETE_USER_SOCIAL_ACCOUNT: (feedId, networkId, callBack) => dispatch(DELETE_USER_SOCIAL_ACCOUNT(feedId, networkId, callBack)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(SocialAccount))