import { GET_POST, GET_WALL_DATA_NEW, MODERATION_POST_GET_AUTO_COMPLETE, SHARE_CODE, UPDATE_PUBLISH_TAGS, UPDATE_WALL_NAME } from "./api";
import HttpClient from "./http-client";
export const IS = localStorage.getItem('token');


export const generatetagCode = (tags) => {

    return new HttpClient().post(`${UPDATE_PUBLISH_TAGS}`, {
        'ownerId': localStorage.getItem('ownerId'),
        'wallId': localStorage.getItem('wallId'),
        'tags': tags.join(),
    });
}


export const getWall = () => {
    return new HttpClient().get(`${GET_WALL_DATA_NEW}`, { headers: { editor: 1 } })
}


export const autoCompleteSearchPublish = (keyword, db_table) => {
    const type = 1;
    return new HttpClient().post(`${MODERATION_POST_GET_AUTO_COMPLETE}`, { ownerId: localStorage.getItem('ownerId'), db_table: db_table, keyword: keyword, wallId: localStorage.getItem('wallId'), feeds: "", type })
}

export const shareCode = (code) => {
    return new HttpClient().post(`${SHARE_CODE}`, {
        'htmlCode': code.htmlCode,
        'type': code.type,
        'email': code.email,
    })
}

export const updateWall = (getdata) => {
    return new HttpClient().post(`${UPDATE_WALL_NAME}`, getdata)
}
